/* src/app/header/header.scss */
header {
  background-color: #fff8e1;
  font-family: "Roboto", sans-serif;
}
.navbar-brand span {
  color: #d97706;
}
.nav-link,
.dropdown-item {
  color: #b66407 !important;
  transition: color 0.3s ease;
}
.nav-link:hover,
.dropdown-item:hover {
  color: #d38c07 !important;
  background-color: #fef3c7;
}
.dropdown-menu {
  background-color: #fff8e1;
  border: 1px solid #d97706;
}
.dropdown-menu.show {
  display: block;
}
.active-route {
  color: #b16106 !important;
  font-weight: bold;
  background-color: rgba(233, 206, 97, 0.8823529412);
}
.basket-icon {
  color: red;
  font-size: 1.5rem;
  line-height: 1;
}
.panier {
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fef3c7;
  border-radius: 50%;
  border: 2px solid #d97706;
  transition: background-color 0.3s ease, border-color 0.3s;
}
/*# sourceMappingURL=header.css.map */
